@import "../commond/styles/sass/variables";
@import "../commond/styles/sass/mixins";

.mainBlock {
  min-height: 100vh;
  background-color: #494b50;

  .mainContainer {
    padding-top: 30vh;
    @media screen and (max-width: 795px) {
      justify-content: center;
      text-align: center;
      padding-top: 0;
    }
    .text {
      width: 300px;
      margin: 5px;
    }

    .photo {
      width: 400px;
      height: 400px;
      background-color: $textGreyLight;
      background-size: cover;
      background-position: center;
      background-repeat: no-repeat;
      border-radius: 50%;
      margin: 5px;
      @media screen and (max-width: 952px) {
        width: 300px;
        height: 300px;
      }
    }

    .lickBlock {
      width: 70%;
      min-width: 284px;
      max-width: 300px;
      display: flex;
      justify-content: space-between;
      margin: 5px;
    }
  }
}







